.publication {
	min-height: 100px;
	width: 100%;
	margin-top: 30px;
	padding-bottom: 20px;
}

.section:nth-child(odd) .publication:not(:last-child) {
	border-bottom: 2px solid var(--background_dark);
}

.section:nth-child(even) .publication:not(:last-child) {
	border-bottom: 2px solid var(--background_light);
}

.publication .text {
	display: inline-block;
	width: calc(99% - 125px);
}

.publication .text:only-child {
	width: 100%
}

.publication .header {
	margin-bottom: 5px;
}

.publication .header .title {
	font-size: 1.5em;
	color: var(--link);
}

.publication .header .title a:hover {
	text-decoration: none;
}

.publication .footer .pdf a:hover {
	color: red;
	text-decoration: none;
}

.publication .footer {
	font-size: 0.9em;
	text-align: right;
}

.publication .abstract {
	text-indent: 40px;
	margin-bottom: 15px;
	text-align: justify;
}

@media screen and (min-width: 1100px) {
	.publication .abstract {
		text-align: justify;
	}

}

.publication .authors, .publication .authors a {
	margin-bottom: 15px;
	color: var(--text-light);
}

.publication .authors a:hover {
	color: var(--text);
}

.publication .authors .big {
	font-variant: small-caps;
	font-size: 1.2em;
}

.publication .footer>span {
	margin-left: 20px;
	color: var(--text-light);
}

.publication .footer>span>a {
	color: var(--text-light);
}

.publication .icon {
	vertical-align: top;
	display: inline-block;
	height: 125px;
	width: 125px;
	margin-right: 1%;
}

.publication .icon img {
	width: 125px;
}

@media screen and (max-width: 1100px) {
	.publication .icon {
		display: none;
	}

	.publication .text {
		width: 100%;
	}
}